@font-face {
  font-family: 'Nickainley';
  src: url('./fonts/Nickainley-Normal.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

.hamburger {
  display: none;
  position: absolute;
  top: 2rem;
  right: 2rem;
  width: 32px;
  height: 32px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: none;
  z-index: 3;
  cursor: pointer;
}

.welcome-block {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0;
  width: 100%;
  box-sizing: border-box;
}

.welcome-citation {
  background-color: #637b89;
  padding: 1rem 2rem;
  border-radius: 20px;
  border: #ff7e20 solid 2px;
  font-style: italic;
  font-weight: bold;
  font-size: 1.5rem;
  color: #ffffff;
  text-align: right;
  margin: 0 2.5rem 2.5rem 0;
  font-family: 'DM Sans', Arial, sans-serif;
}

.welcome-citation-2 {
  font-style: italic;
  font-family: 'Nickainley', cursive;
  font-size: 2rem;
  color: #585858;
  text-align: right;
  margin: 1em 2.5rem 2.5rem 0;
}

.soumission-btn {
  background: #ff7e20;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 2rem 4rem;
  font-size: 1.5rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.soumission-btn:hover,
.soumission-btn:focus {
  background: #ff993f;
  outline: none;
}

.soumission-btn-fixed {
  position: absolute;
  right: 2.5rem;
  bottom: 2.5rem;
  z-index: 2;
}

.hamburger span {
  display: block;
  width: 100%;
  height: 4px;
  margin: 4px 0;
  background: #282c34;
  border-radius: 2px;
  transition: 0.3s;
}

.hamburger.open span:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.hamburger.open span:nth-child(2) {
  opacity: 0;
}

.hamburger.open span:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

.nav-top {
  position: absolute;
  top: 2rem;
  right: 2rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  z-index: 2;
}

.nav-top a {
  display: inline;
}

.header-logo {
  position: absolute;
  top: 2rem;
  right: 2rem;
  width: 200px;
  height: auto;
  z-index: 4;
}

@media (max-width: 900px) {
  .hamburger {
    display: flex;
  }
  
  .header-logo {
    width: 120px;
    top: 1rem;
    right: 4rem;
  }
  
  .nav-top {
    flex-direction: column;
    align-items: flex-end;
    background: rgba(255, 255, 255, 0.98);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    padding: 1rem;
    transition: transform 0.3s, opacity 0.3s;
    opacity: 0;
    pointer-events: none;
    transform: translateY(-20px);
    top: 4rem;
    right: 1rem;
  }
  
  .nav-top.nav-open {
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0);
  }
  
  .nav-top a {
    display: block;
  }
}

.App {
  font-family: 'DM Sans', Arial, sans-serif;
  color: #555555;
  margin: 0;
  padding: 0;
  background: #eeecea;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}


.header {
  background: url('../public/portrait.png') left center no-repeat;
  background-size:contain;
  
  min-height: 100vh;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}

.welcome-content {
  padding: 2rem 2.5rem 0rem 0;
  margin-left: auto;
  margin-right: 0;
  align-self: flex-end;
  max-width: 90vw;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  border-radius: 16px;
  text-align: right;
}

.welcome-flex {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  gap: 0;
  text-align: right;
}

.welcome-flex h1 span {
  color: #ff7e20
}

.welcome-logo {
  width: 200px;
  height: auto;
}

.welcome-content h1 {
  margin-bottom: 0rem;
  font-size: 2.5rem;
}

.logo {
  width: 80px;
  margin-bottom: 1rem;
}

.nav {
  margin-top: 1rem;
  display: flex;
  gap: 1.5rem;
  color: #555555;
}

.nav a {
  color: #555555;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.5rem;
}

.nav a:visited,
.nav a:active,
.nav a:hover,
.nav a:focus {
  color: #555555;
  text-decoration: none;
}

.nav a:hover {
  text-decoration: underline;
}


.main {
  flex: 1;
  padding: 2rem 1rem;
  width: 100%;
  max-width: none;
  margin: 0;
  box-sizing: border-box;
}

.main section {
  margin-bottom: 2rem;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  padding: 1.5rem;
}

.footer {
  background: #282c34;
  color: #fff;
  text-align: center;
  padding: 1rem;
  font-size: 0.95rem;
}
